import style from "../InstituteFeedback/InstituteFeedback.module.css";
import {
  assestsFeedUrl,
  assestsUserFinanceUrl,
} from "../../Services/UrlConfig/AssestsBaseUrl";
import { imageShowUrl } from "../../Services/UrlConfig/BaseUrl";
import dayjs from "dayjs";
import { useTranslation } from "react-i18next";
import { useState } from "react";
import InstituteUniversalBatchCardMenu from "./InstituteUniversalBatchCardMenu";
const InstituteUniversalBatchCard = ({ batch, instituteId, onRefetch }) => {
  const { t } = useTranslation();
  const [open, setOpen] = useState(false);
  const onToggle = () => {
    setOpen((pre) => !pre);
  };
  return (
    <>
      <div
        className={
          batch?.activeBatch === "Active"
            ? `${style.sf_card} ${style.sf_card_active}`
            : style.sf_card
        }
        style={{
          padding: "0.5rem 1rem",
        }}
      >
        <div className={style.sf_card_contatiner}>
          <img
            src={`${assestsUserFinanceUrl}/batch.svg`}
            loading="lazy"
            alt="avatar"
            style={{
              borderRadius: "0",
            }}
          />

          <div className={style.sf_card_contatiner_inner}>
            <h6>{batch?.batchName ?? ""}</h6>
            <p>
              {t("created_on_")}
              {": "}
              {dayjs(batch?.createdAt).format("DD/MM/YYYY")}
            </p>
          </div>
        </div>
        <img
          src={`${assestsUserFinanceUrl}/menu.svg`}
          alt="menu icon"
          onClick={onToggle}
        />
      </div>
      {open && (
        <InstituteUniversalBatchCardMenu
          onClose={onToggle}
          instituteId={instituteId}
          menuData={batch}
          onRefetch={onRefetch}
        />
      )}
    </>
  );
};

export default InstituteUniversalBatchCard;
