import { useTranslation } from "react-i18next";
import ModalMenu from "../../Ui/Modal/ModalMenu";
import { useState } from "react";
import { useInstituteSelectUniversalBatch } from "../../Hooks/ApiHooks/DepartmentManager/department-manager-api";

const InstituteUniversalBatchCardMenu = ({
  instituteId,
  onClose,
  menuData,
  onRefetch,
}) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);

  const [instituteSelectUniversalBatch] = useInstituteSelectUniversalBatch();

  const onDelete = () => {
    if (menuData?._id && instituteId) {
      setDisabled((pre) => !pre);
      instituteSelectUniversalBatch({
        id: instituteId,
        bodyParameter: {
          batchId: menuData?._id,
        },
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };

  return (
    <>
      <ModalMenu onClose={onClose} disabled={disabled}>
        <h6 onClick={onDelete}>{t("select_batch")}</h6>
      </ModalMenu>
    </>
  );
};

export default InstituteUniversalBatchCardMenu;
