import { Suspense, lazy, useCallback, useEffect, useState } from "react";
import { assestsNavbarUrl } from "../../Services/UrlConfig/AssestsBaseUrl";
import CreateButton from "../../Ui/Button/CreateButton";
import BorderBottom from "../../Ui/Border/BorderBottom";
import UserMemberContentWrapper from "../../Ui/UserMember/UserMemberContentWrapper";
import UserMemberContainerWrapper from "../../Ui/UserMember/UserMemberContainerWrapper";
import QvipleLoader from "../../Loader/QvipleLoader";
import Loading from "../../Loader/Loading";
import EmptyMessage from "../../Utils/EmptyMessage/EmptyMessage";
import { useInView } from "react-intersection-observer";
import { nextPage } from "../../Utils/Functions/nextPage";
import { customDebounce } from "../../Utils/Functions/debounce";
import { useTranslation } from "react-i18next";
import style from "../../UserStaffMember/FinanceManager/FinanceManager.module.css";
import { useSelector } from "react-redux";
import { useInstituteAllUniversalBatch } from "../../Hooks/ApiHooks/DepartmentManager/department-manager-api";
import AddInstituteUniversalBatch from "./AddInstituteUniversalBatch";
import InstituteUniversalBatchCard from "./InstituteUniversalBatchCard";
const InstituteUniversalBatch = () => {
  const { t } = useTranslation();
  const instituteId = useSelector((state) => state.idChange.id);
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });
  const [add, setAdd] = useState(false);
  const {
    instituteAllUniversalBatch,
    instituteAllUniversalBatchLoading,
    instituteAllUniversalBatchRefetch,
  } = useInstituteAllUniversalBatch({
    data: {
      id: instituteId,
      page: page,
      limit: 10,
      search: search,
    },
    skip: !instituteId,
  });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (instituteAllUniversalBatch?.list?.length) {
      let obj = nextPage(instituteAllUniversalBatch?.list?.length);
      setState(() => obj);
    }
  }, [instituteAllUniversalBatch?.list]);

  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setPage(() => 1);
      setSearch("");
    }
  };

  const onRefetch = useCallback(() => {
    setPage(() => 1);
    setSearch("");
    instituteAllUniversalBatchRefetch();
  }, [instituteAllUniversalBatchRefetch]);

  const onToggle = () => {
    setAdd((pre) => !pre);
  };
  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <h6>{t("universal_batch")}</h6>
            <div className={style.fm_search}>
              <section className={style.fm_search_container}>
                <div className={style.fm_search_container_inner}>
                  <img
                    className={style.fm_search_icon}
                    alt="search icon"
                    src={`${assestsNavbarUrl}/navbar-search.svg`}
                  />
                  <input
                    type="text"
                    placeholder={t("search")}
                    onChange={onSearchEvent}
                  />
                </div>
              </section>
              <CreateButton
                label={t("add")}
                customStyle={{
                  margin: "0",
                  padding: "0.4rem 1rem",
                }}
                onAction={onToggle}
              />
            </div>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper>
          {instituteAllUniversalBatch?.list?.map((batch, index) =>
            instituteAllUniversalBatch?.list?.length === index + 1 ? (
              <div key={batch?._id} ref={ref}>
                <InstituteUniversalBatchCard
                  batch={batch}
                  onRefetch={onRefetch}
                  instituteId={instituteId}
                />
              </div>
            ) : (
              <InstituteUniversalBatchCard
                batch={batch}
                key={batch?._id}
                onRefetch={onRefetch}
                instituteId={instituteId}
              />
            )
          )}
          {instituteAllUniversalBatchLoading ? (
            search ? (
              <QvipleLoader />
            ) : (
              <Loading />
            )
          ) : search ? (
            instituteAllUniversalBatch?.list?.length === 0 && (
              <EmptyMessage title="No batch found related this search." />
            )
          ) : (
            instituteAllUniversalBatch?.list?.length === 0 && (
              <EmptyMessage title="No batch." />
            )
          )}
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
      {add && (
        <Suspense fallback={<QvipleLoader />}>
          <AddInstituteUniversalBatch
            onClose={onToggle}
            onRefetch={onRefetch}
            instituteId={instituteId}
          />
        </Suspense>
      )}
    </>
  );
};

export default InstituteUniversalBatch;
